(function() {

  'use strict';

  angular
    .module('musicHead')
    .directive('navBar', navBar);

    navBar.$inject = ['YT_event','cachingFactory','toastr'];

    function navBar() {

    return {
      restrict: "E",
      templateUrl: 'app/components/NavBar/navBar.template.html',
      controller: navBarController,
      controllerAs: 'navBarCtrl'
    }

    function navBarController($timeout, dataService, YT_event, $scope, cachingFactory, toastr) {

      var vm = this;

      vm.logOut = logOut;
      vm.pause = pause;
      vm.play = play;
      vm.stop = stop;
      vm.next = next;
      vm.setVolume = setVolume;
      vm.mute = mute;
      vm.muted = false;
      vm.volume = 100;
      vm.profilePictureUrl;
      vm.facebookLogFlag;
      vm.inputApproachFlag = cachingFactory.approachFlag;

      dataService.checkLogStatus()
        .then(setFlag)
        .then(getProfilePicture)
        .then(setProfilePictureUrl)
        .catch(function(error) {
          toastr.error(error);
        })

      function setFlag(response) {
        vm.facebookLogFlag = response.status === 'connected';
        return vm.facebookLogFlag;
      }

      function getProfilePicture(facebookLogFlag) {
        if(facebookLogFlag) {
            return dataService.getProfilePicture();
          }
      }

      function setProfilePictureUrl(picture) {
        if(picture) {
          vm.profilePictureUrl = picture.data.url;
        } else {
          return;
        }
      }

      function logOut() {
        dataService.logOutFromFb();
      }

      function pause() {
        $scope.$broadcast(YT_event.PAUSE);
      }

      function play() {
        $scope.$broadcast(YT_event.PLAY);
      }

      function stop() {
        $scope.$broadcast(YT_event.STOP);
      }

      function next() {
        $scope.$broadcast(YT_event.NEXT);
      }

      function setVolume() {
        $scope.$broadcast(YT_event.VOLUME, vm.volume);
      }

      function mute() {
        vm.muted=!vm.muted;
        $scope.$broadcast(YT_event.MUTE);
      }
    }
  }
})();



(function() {

  'use strict';

  angular
    .module('musicHead')
    .directive('favourite', favourite);

    favourite.$inject = ['YT_event','cachingFactory','toastr'];

    function favourite() {

    return {
      restrict: "E",
      templateUrl: 'app/components/NavBar/favourite.template.html',
      controller: favouriteController,
      controllerAs: 'fvCtrl'
    }

    function favouriteController($timeout, dataService, YT_event, $scope, cachingFactory, toastr, favouritesService) {

      var vm = this;

      vm.playlists = favouritesService.favourites;
      vm.activePlaylist = favouritesService.activePlaylist;
      vm.toggleActive = favouritesService.toggleActive;
      vm.setActivePlaylist = setActivePlaylist;
      vm.addNewPlaylist = addNewPlaylist;
      vm.addingNewPlaylistStatus = false;
      vm.addingNewPlaylist = addingNewPlaylist;
      vm.makeXshowPlaylist = favouritesService.makeXshowPlaylist;
      vm.makeXhidePlaylist = favouritesService.makeXhidePlaylist;
      vm.makeXshowArtist = favouritesService.makeXshowArtist;
      vm.makeXhideArtist = favouritesService.makeXhideArtist;
      vm.removePlaylist = favouritesService.removePlaylist;
      vm.removeArtist = favouritesService.removeArtist;
      vm.showPlaylists = showPlaylists;
      vm.hidePlaylists = hidePlaylists;
      vm.playlistsVisible = false;
      vm.playlistsVisibleTimeout = false;
      vm.showArtists = showArtists;
      vm.hideArtists = hideArtists;
      vm.artistsVisible = false;
      vm.artistsVisibleTimeout = false;

      favouritesService.checkLocalStorage();

      function showPlaylists () {
        if (vm.artistsVisible === false) {
          vm.playlistsVisible = true;
          vm.playlistsVisibleTimeout = false;
        }
      }

      function hidePlaylists () {
        vm.playlistsVisibleTimeout = true;
        $timeout(function() {
          if (vm.playlistsVisibleTimeout) {
            vm.playlistsVisible = false;
          }
        }, 200);
      }

      function showArtists () {
        vm.artistsVisible = true;
        vm.artistsVisibleTimeout = false;
      }

      function hideArtists () {
        vm.artistsVisibleTimeout = true;
        $timeout(function() {
          if (vm.artistsVisibleTimeout) {
            vm.artistsVisible = false;
          }
        }, 200);
      }

      function setActivePlaylist (playlist) {
        vm.playlistsVisible = false;
        favouritesService.setActivePlaylist(playlist);
      }

      function addingNewPlaylist () {
        vm.addingNewPlaylistStatus = true;
        vm.newPlaylist = '';
      }

      function addNewPlaylist (playlist) {
        if (playlist) {
          favouritesService.addNewPlaylist(playlist);
        }
        $timeout(function() {
          vm.addingNewPlaylistStatus = false;
        }, 50);
      }
    }
  }
})();

(function() {

  'use strict';

  angular
    .module('musicHead')
    .factory('helpersFactory', helpersFactory);

  function helpersFactory() {

  // Accessible members

    var factory = {
      trimmingArray: trimmingArray,
      shuffle: shuffle,
      formatTime: formatTime
    };

  // Returning object of functions

    return factory;

  // Functions declatarions

    function trimmingArray(arr) {
      var trimmedArray = [];
      angular.forEach(arr, function(item) {
        item = item.trim();
        if (-1 === trimmedArray.indexOf(item)){
          trimmedArray.push(item);
        }
      });
      return trimmedArray;
    }

    function shuffle(array) {
      var currentIndex = array.length, temporaryValue, randomIndex;
      // While there remain elements to shuffle...
      while (0 !== currentIndex) {
      // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;
        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
      }
      return array;
    }

    // Function for formating time into minutes and seconds

      function formatTime(time) {
        time = Math.round(time);
        var minutes = Math.floor(time / 60),
        seconds = time - minutes * 60;
        seconds = seconds < 10 ? '0' + seconds : seconds;
        return minutes + ":" + seconds;
      }

  }
})();

